<template>
    <box :data="window"/>
</template>
<script>
import box from './i.v.s.box.vue';
export default {
    name:"WINDOW",
    components:{
        box
    },
    props:["data"],
    data()
    {
        return {}
    },
    computed:{
        window(){
            
            return {
                boxStyle:{
                    columns:'2em auto 6em',
                    rows:'minmax(24vmin,1fr) auto 2em'
                },
                ...this.data,
                is:"box",
                head:{
                        is:'navBar',
                        navStyle:this.params.navStyle, 
                        title:this.params.title,
                        subtitle:this.params.subtitle
                }
            }
}
}}
</script>
<style lang="less">
.WINDOW.BOX
{
    
    --height-navBar:7em;
    width:100vw !important;
    height: 100vh !important;
    overflow: hidden !important;

}
</style>



